.paginationContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  margin-bottom: 75px;
}
.totalProductsCount {
  background-color: #d9e0c9a6;
  color: #4a8015;
  padding: 2px 10px;
  border-radius: 12px;
  margin-right: 3px;
}
.products {
  color: #a9a9a9;
}
.arrow {
  margin-left: 10px;
  transform: rotate(90deg);
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  letter-spacing: 1.1px;
  font-weight: 600;
  font-size: 1rem;
  height: 47px;
  width: 230px;
  background-color: #6a983c;
  border-radius: 12px;
  border: 2px solid #46760a;
  cursor: pointer;
}
.pagination {
  width: fit-content;
}
.mobilePagination {
  display: none;
}
@media (max-width: 768px) {
  .button {
    letter-spacing: 0px;
    height: 40px;
    padding: 10px;
    width: fit-content;
    font-size: 0.9rem;
  }

  .mobilePagination {
    display: block;
  }
  .paginationContainer {
    margin-top: 15px;
    flex-direction: column-reverse;
  }
  .pagination,
  .totalProductsCount,
  .countContainer {
    display: none;
  }
}
